@import 'base';


header {
    background-color: $primarycolor !important;
    padding: 3em 5em;
}

.logo {
    font-family: 'Kadwa', serif;
    font-size: 1.8em !important;
    color: $btncolor !important;
    transition: 0.3s ease;

    &:hover {
        color: darken($btncolor, 10%) !important;
    }
}


.navbar-toggler {

    .navbar-toggler-icon {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='white' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e") !important;
    }
}

.navbar-collapse {
    .dropdown-menu {
        background-color: $primarycolor;
        border: none !important;

        a {
            &:hover {
                background-color: darken($primarycolor, 10%);
            }
        }

        li {
            &:hover {
                background-color: darken($primarycolor, 10%);
            }
        }
    }

    li {
        padding: 0.5em;
    }

    a {
        color: darken($white, 15%);

        &:hover {
            color: $white;

        }
    }
}

.wrapper-main {
    @include stylefont();
}




.wrapper-one {
    padding: 3em 0 3em 10em;
    @include grid(1fr 1fr, $secondarycolor, 30em);

    @include stylefont();

    img {
        width: 100%;

    }

}



.wrapper-two {
    @include grid(1fr 0.9fr, $primarycolor, fit-content);
    padding: 6em 10em 6em 10em;
    column-gap: 1em;

    @include stylefont($secondarycolor);

    .grid-two-wrapper-two {
        padding-bottom: 2em !important;
        color: #FCF4ED;
    }

    .grid-one-wrapper-two {
        padding-bottom: 0 !important;
    }


    img {
        width: 75% !important;
    }

    button {
        @include stylebtn();

        &:hover {
            @include stylebtn-hov();
        }
    }
}





.wrapper-three {
    @include grid(1fr 1fr, $secondarycolor, fit-content);
    padding: 5em 13em 5em 13em;
    column-gap: 1em;
    text-align: center;
    margin: auto;

    @include stylefont();

}



