.rounded-img {
    background-color: $white;
    border: 0.5px solid $btncolor;
    height: 7em;
    width: 8em;
    border-radius: 10%;
    display: inline-block;
    margin-bottom: 1em;
    padding: 1.2em;
    transition: 0.3s ease;

    &:hover {
        @extend .rounded-img;
        background-color: $secondarycolor;
        padding: 1.4em;
    }
}


.wrapper-public {
    padding: 3em 3em 3em 10em;
    @include grid(1fr, $secondarycolor, fit-content);

    @include stylefont();

}


footer {
    height: fit-content;
    background-color: $primarycolor !important;
    margin: 0 auto;
    color: rgb(236, 236, 236);

    .footer-top {
        margin: auto;
        padding: 5em 5em 6em 5em;
        width: 75%;
        text-align: center;
    }

    h1 {
        font-size: 1.2em;
        font-style: italic;
        font-weight: 300;
    }

}





@media screen and (max-width: $ipad) {

    header {
        padding: 2em 2em;
    }

    .navbar-collapse {
        .dropdown-menu {
            background-color: $primarycolor;
            border: none !important;

            a {
                &:hover {
                    background-color: $primarycolor;
                }
            }

            li {
                &:hover {
                    background-color: $primarycolor;
                }
            }
        }
    }

    .wrapper-one {
        @include grid(1fr, $secondarycolor, 40em);
        padding: 2em 2em 2em 2em;
        text-align: center;
        margin: auto;

        @include stylefont($desktop: false);

        img {
            width: 75% !important;
        }
    }

    .wrapper-two {
        @include grid(1fr, $primarycolor, fit-content);
        padding: 3em 2em 3em 2em;
        column-gap: 2em;
        text-align: center;
        margin: auto;

        img {
            width: 75% !important;
        }

        @include stylefont($desktop: false);

    }

    .wrapper-three {
        @include grid(1fr 1fr, $secondarycolor, fit-content);
        padding: 3em 2em 3em 2em;
        text-align: center;
        margin: auto;

        @include stylefont($desktop: false);

    }

}





