    }

    .wrapper-public {
        // @include grid(1fr, $secondarycolor, fit-content);
        padding: 3em 2em 3em 2em;
        column-gap: 2em;

        @include stylefont($desktop: false);

    }

    .rounded-img {
        background-color: $white;
        border: 0.5px solid $btncolor;
        height: 5em;
        width: 5.5em;
        border-radius: 10%;
        padding: 0.9em;

        &:hover {
            padding: 1em;
        }
    }
}




@media screen and (min-width: 1500px) {
    .wrapper-one {

        img {

            width: 75%;
            float: right;

        }
    }
}