
//VARIABLES

//colors
$primarycolor: #121212;
$secondarycolor:#FCF4ED;
$elementcolor: #2b2b2b;
$white: #fff;
$black: #000;
$btncolor: #FCF4ED;
//fonts
$titlefont:  'Raleway', sans-serif;
$textfont: 'Montserrat', sans-serif;


//units
$iphone: 771px;
$ipad: 992px;



//MIXINS

//buttons
@mixin stylebtn {
    padding: 0.5em 1.3em;
    background-color: $btncolor;
    color: $primarycolor;
    border: 0.5px solid $primarycolor;
    font-weight: 500;
}

@mixin stylebtn-hov {
    transition: 0.3s ease;
    padding: 0.5em 1.3em;
    background-color: darken($btncolor, 10%);
    border: 0.5px solid $primarycolor;
}

//font dif units
@mixin stylefont($desktop: true) {

    @if $desktop {
        h1 {
            font-weight: 700;
            font-size: 2.5em;
            font-family: $titlefont;
        }

        p {
            font-size: 1.2em;
            color: $elementcolor;
            font-family: $textfont;
        }
    }

    @else {
        h1 {
            font-size: 2em;
        }

        p {
            font-size: 1.1em;
        }
    }

}

//grid
@mixin grid($grid-temp, $bgcolor, $height) {
    display: grid;
    grid-template-columns: $grid-temp;
    background-color: $bgcolor;
    height: $height;
}